import React, { Component } from "react";
import {
  Grid,
  Button,
  Accordion,
  AccordionSummary,
  AccordionDetails,
  Typography,
} from "@material-ui/core";
import { Group, CheckBox, FilterList, ErrorRounded } from "@material-ui/icons";
import { connect } from "react-redux";
import IconButton from "@material-ui/core/IconButton";

import Loading from "../../Components/Loading";
import BarchartData from "./Barchart";
import VerticleList from "./VerticleList";

import { getDefaultActivities } from "../../Redux/activityRedux/activityAction";
import { getAllUpdatesDetails } from "../../Redux/overviewRedux/overviewAction";
import axios from "axios";
import {Link} from 'react-router-dom';

class Overview extends Component {
  constructor(props) {
    // console.log(iterList);
    super(props);
    const role = global.config.secureStorage.getItem("role");
    const roles = ["project_manager", "chief_engineer", "super_admin"];
    // const isAddVisible = roles.find(item => item == role) ? true : false;

    this.state = {
      subProject: {},
      loading: true,
      activeActivityIndex: 0,
      percentage: [],
      accNames: [],
      projectType: "",
      activityDetails: [],
      safetyCheckList : 0,
      safetyIssue : 0,
      qualityCheckList : 0,
      qualityIssue : 0,
      users: 0,
      actualCompletionByPercent:[],
      estimatedCompletionByPercent:[],
      actualCompletionByCost:[],
      estimatedCompletionByCost:[],
      activityNames : [],
    };
  }

  async componentDidMount() {
    try {
      await this.props.getDefaultActivities(this.state.activeActivityIndex);
      await this.props.getAllUpdatesDetails(
        global.config.secureStorage.getItem("subprojectId")
      );

      const response = await axios.get(
        `${global.config.backendURL}/overview/getAllStatisticalData/${global.config.secureStorage.getItem("projectId")}/${global.config.secureStorage.getItem("subprojectId")}`
      );
      response.data.forEach(data=>{
        if(data.type === 'checklist') {
          this.setState({safetyCheckList : data.safety, qualityCheckList:  data.quality});
        }
        if(data.type === 'issue') {
          this.setState({safetyIssue : data.safety, qualityIssue: data.quality });
        }
        if(data.type === 'users') {
          this.setState({users : data.total});
        }        
      })
      const monitoringRes = await axios.get(
        `${global.config.backendURL}/monitoring/getActivities/${global.config.secureStorage.getItem("subprojectId")}/${global.config.secureStorage.getItem("projectId")}`
      );
      let estimatedCompletionByPercent = [];
      let actualCompletionByPercent = [];
      let estimatedCompletionByCost = [];
      let actualCompletionByCost = [];
      let activityNames = [];
      monitoringRes.data.forEach(data=>{
        let tempData = JSON.stringify(data);
        tempData = JSON.parse(tempData);

        if(tempData.estimatedcompletion_by_cost > 0 || (tempData.estimatedcompletion.toLowerCase()!="not available" && Number(tempData.estimatedcompletion.split('%')[0]) > 0) || tempData.actualcompletion_by_cost > 0 ||  (tempData.actualcompletion.toLowerCase()!="not available" && Number(tempData.actualcompletion.split('%')[0]) > 0)){
          estimatedCompletionByPercent.push(tempData.estimatedcompletion.toLowerCase()!="not available" ? Number(tempData.estimatedcompletion.split('%')[0]): 0);
          actualCompletionByPercent.push(tempData.actualcompletion.toLowerCase()!="not available"? Number(tempData.actualcompletion.split('%')[0]): 0);
          estimatedCompletionByCost.push(isNaN(tempData.estimatedcompletion_by_cost) ? 0 : tempData.estimatedcompletion_by_cost );
          actualCompletionByCost.push(isNaN(tempData.actualcompletion_by_cost) ? 0 : tempData.actualcompletion_by_cost);
          activityNames.push(tempData.title);            
        }
      });
      
      let newPer = [];
      let newData = [];
      let DataAcc = this.props.percentage.accName;
      let DataList = this.props.percentage.per;
      DataList.forEach((element, i) => {
        if (element != 0) {
          newPer.push(element);
          newData.push(DataAcc[i]);
        }
      });
      if (this.props.activityDetails)
        this.setState({
          activityDetails: this.props.activityDetails,
        });
      this.setState({
        loading: !this.state.loading,
        percentage: newPer,
        accNames: newData,
        estimatedCompletionByCost : estimatedCompletionByCost,
        estimatedCompletionByPercent : estimatedCompletionByPercent,
        actualCompletionByCost : actualCompletionByCost,
        actualCompletionByPercent : actualCompletionByPercent,
        activityNames : activityNames
      });
      // this.fetchData();
    } catch (error) {
      //
    }
  }

  render() {
    if (this.props.loading) {
      return <Loading loading={this.props.loading} />;
    }
    return (
      <div className="container" style={{ marginTop: "20px" }}>
        <Grid container spacing={2}>
          <Grid
            style={{
              backgroundColor: "#fff",
            }}
            item
            sm={8}
            xs={12}
          >
            <Grid container spacing={2}>
              
              <Grid className="mt-2" item sm={2} xs={6}>
                <Grid
                  className="p-4"
                  style={{
                    textAlign: "center",
                    height: "100%",
                    border: "1px solid #ddd",
                  }}
                >
                  <Link to="/editUser">
                  <IconButton aria-label="settings">
                    <Group fontSize="large" color="disabled" />
                  </IconButton>
                  </Link>
                  <Typography variant="h6">{this.state.users}</Typography>
                  <Typography variant="h6">Users</Typography>
                </Grid>
              </Grid>
              <Grid className="mt-2" item sm={2} xs={6}>
                <Grid
                  className="p-4"
                  style={{
                    textAlign: "center",
                    height: "100%",
                    border: "1px solid #ddd",
                  }}
                >
                  <Link to="/checklist/quality">
                  <IconButton aria-label="settings">
                    <FilterList fontSize="large" color="disabled" />
                  </IconButton>
                  </Link>
                  <Typography variant="h6">{this.state.qualityCheckList}</Typography>
                  <Typography variant="h6">Quality Check list</Typography>
                </Grid>
              </Grid>
              <Grid className="mt-2" item sm={2} xs={6}>
                <Grid
                  className="p-4"
                  style={{
                    textAlign: "center",
                    height: "100%",
                    border: "1px solid #ddd",
                  }}
                >
                  <Link to="/checklist/safety">
                  <IconButton aria-label="settings">
                  <FilterList fontSize="large" color="disabled" />
                  </IconButton>
                  </Link>
                  <Typography variant="h6">{this.state.safetyCheckList}</Typography>
                  <Typography variant="h6">Safety Check list</Typography>
                </Grid>
              </Grid>
              <Grid className="mt-2" item sm={2} xs={6}>
                <Grid
                  className="p-4"
                  style={{
                    textAlign: "center",
                    height: "100%",
                    border: "1px solid #ddd",
                  }}
                >
                  <Link to="/issues/quality">
                  <IconButton aria-label="settings">
                    <ErrorRounded fontSize="large" color="disabled" />
                  </IconButton>
                  </Link>
                  <Typography variant="h6">{this.state.qualityIssue}</Typography>
                  <Typography variant="h6">Quality Issue</Typography>
                </Grid>
              </Grid>
              <Grid className="mt-2" item sm={2} xs={6}>
                <Grid
                  className="p-4"
                  style={{
                    textAlign: "center",
                    height: "100%",
                    border: "1px solid #ddd",
                  }}
                >
                  <Link to="/issues/safety">                  
                  <IconButton aria-label="settings">
                    <ErrorRounded fontSize="large" color="disabled" />
                  </IconButton>
                  </Link>
                  <Typography variant="h6">{this.state.safetyIssue}</Typography>
                  <Typography variant="h6">Safety Issue</Typography>
                </Grid>
              </Grid>
            </Grid>
          </Grid>
          <Grid item sm={4} xs={12}>
          <VerticleList activityDetails={this.state.activityDetails} />
          </Grid>
        </Grid>

        <Grid container className="mt-4" spacing={2}>
          <Grid item sm={4} xs={12}>
            {/* {console.log(this.state.accNames)} */}
            {this.state.accNames.length > 0 ? (
              <BarchartData
                accNames={this.state.accNames}
                percentage={this.state.percentage}
                percentageLabel = "Activity Updates"
              />
            ) : (
              ""
            )}
          </Grid>
          <Grid item sm={4} xs={12}>
            {/* {console.log(this.state.accNames)} */}
            {this.state.activityNames && this.state.estimatedCompletionByPercent && this.state.activityNames.length>0 ?
            <BarchartData
                accNames={this.state.activityNames}
                percentage={this.state.estimatedCompletionByPercent}
                percentageLabel = "Estimated Completion by Percentage"
                percentage1= {this.state.actualCompletionByPercent}
                percentage1Label = "Actual Completion by Percentage"
              />
            :''}
          </Grid>          
          <Grid item sm={4} xs={12}>
            {/* {console.log(this.state.accNames)} */}
            {this.state.activityNames && this.state.actualCompletionByCost && this.state.activityNames.length>0 ?
            <BarchartData
            accNames={this.state.activityNames}
            percentage={this.state.estimatedCompletionByCost}
            percentageLabel = "Estimated Completion by Cost"
            percentage1= {this.state.actualCompletionByCost}
            percentage1Label = "Actual Completion by Cost"
          />
            :''}
          </Grid>
        </Grid>
      </div>
    );
  }
}

const mapStateToProps = state => {
  // console.log(state.overviewReducer);
  return {
    defaultActivity: state.activityReducer.defaultActivity,
    percentage: state.activityReducer.percentage,
    loading: state.activityReducer.loading,
    activityDetails: state.overviewReducer.activityDetails,
  };
};

export default connect(mapStateToProps, {
  getDefaultActivities,
  getAllUpdatesDetails,
})(Overview);
